body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
}
a {
  text-decoration: none;
  text-decoration-color: none;
}
.visually-hidden {
  visibility: hidden;
}
.heading2 {
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 36px;
  line-height: 60px;
  font-weight: 400;
  color: #111111;
}
.heading3 {
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 24px;
  line-height: 36px;
  font-weight: 400;
  color: #111111;
  text-transform: uppercase;
}
.heading4 {
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #111111;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  body {
    font-size: 16px;
    line-height: 20px;
  }
  .heading2 {
    font-family: "Oswald", "Tahoma", sans-serif;
    font-size: 60px;
    line-height: 60px;
    font-weight: 400;
    color: #111111;
  }
  .heading3 {
    font-family: "Oswald", "Tahoma", sans-serif;
    font-size: 36px;
    line-height: 36px;
    font-weight: 400;
    color: #111111;
    text-transform: uppercase;
  }
  .heading4 {
    font-family: "Oswald", "Tahoma", sans-serif;
    font-size: 20px;
    line-height: 20px;
    font-weight: 400;
    color: #111111;
    text-transform: uppercase;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald Regular"), local("Oswald-Regular"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald Medium"), local("Oswald-Medium"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
.button {
  display: block;
  padding: 10px;
  min-width: 280px;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  word-wrap: break-word;
  background-color: #68b738;
  border: none;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 20px;
  }
}
.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 13px 0 10px;
  width: 280px;
  margin: 0 auto;
}
.page-header__toggle {
  padding: 0;
  border: none;
  background-color: transparent;
  width: 25px;
  height: 19px;
}
.page-header__logo[href]:hover,
.page-header__logo[href]:focus {
  opacity: 0.8;
}
.page-header__logo[href]:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .page-header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 708px;
    margin: 0 auto;
    padding: 24px 0;
    margin-bottom: 20px;
  }
  .page-header__wrapper {
    margin: 0;
    padding: 0;
  }
  .page-header__toggle {
    display: none;
  }
  .page-header__logo {
    width: 174px;
    height: 59px;
    max-width: 100%;
  }
  .page-header__logo-image {
    width: 174px;
    height: 59px;
  }
}
@media (min-width: 1300px) {
  .page-header__container {
    position: relative;
    margin: 0 auto;
    margin-bottom: -139px;
    width: 1220px;
    padding-top: 54px;
    z-index: 2;
  }
  .page-header__container--inner {
    margin-bottom: 37px;
  }
  .page-header__logo {
    max-width: 100%;
    width: 203px;
    height: 60px;
  }
  .page-header__logo-image {
    width: 203px;
  }
}
@media (max-width: 767px) and (min-width: 1300px) {
  .page-main__banner {
    width: 100%;
    background-color: linear-gradient(to right, transparent 50%, #65a93c 50%);
  }
}
@media (min-width: 768px) {
  .page-main__banner {
    background: none;
  }
}
.main-nav {
  display: none;
  width: 100%;
}
.main-nav--show {
  display: block;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}
.main-nav__item {
  display: block;
  padding: 22px 0 20px;
  list-style: none;
  text-align: center;
  border-bottom: 1px solid #e6e6e6;
}
.main-nav__link {
  font: inherit;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  color: #111111;
  text-transform: uppercase;
}
.main-nav__link:hover,
.main-nav__link:focus {
  color: rgba(17, 17, 17, 0.6);
}
.main-nav__link:active {
  color: rgba(17, 17, 17, 0.3);
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    margin-left: auto;
    width: auto;
  }
  .main-nav__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    min-width: 440px;
    margin-bottom: -20px;
    border-top: none;
  }
  .main-nav__item {
    margin-bottom: 16px;
    padding: 0;
    border-bottom: 0 none;
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    position: absolute;
    content: "";
    bottom: -9px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
  }
  .main-nav__link {
    font-size: 18px;
    line-height: 25px;
    font-weight: 400;
  }
}
@media (min-width: 1300px) {
  .main-nav__list {
    min-width: 480px;
  }
  .main-nav__item--active::after {
    background-color: #ffffff;
  }
  .main-nav__item--active-inner::after {
    position: absolute;
    content: "";
    bottom: -9px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
  }
  .main-nav__link {
    font-size: 20px;
    color: #ffffff;
  }
  .main-nav__link:hover {
    color: rgba(255, 255, 255, 0.6);
  }
  .main-nav__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
  .main-nav__link--inner {
    color: #111111;
  }
  .main-nav__link--inner:hover,
  .main-nav__link--inner:focus {
    color: rgba(17, 17, 17, 0.6);
  }
  .main-nav__link--inner:active {
    color: rgba(17, 17, 17, 0.3);
  }
}
.page-footer {
  width: 100%;
