.example {
  padding-top: 37px;
  margin-bottom: 0;
  background-color: @gray;
}

.example__container {
  margin: 0 auto;
  width: 320px;
}

.example__title {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 22px;
}

.example__info {
  display: block;
  width: 279px;
  margin: 0 20px;
  margin-bottom: 25px;

  .font(@main-font;
  14px;
  18px;
  400;
  @gray-text);

  text-align: left;
}

.example__description {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 20px;
}

.example__result {
  display: block;
  position: relative;
  margin: 0;
  padding-top: 8px;
  padding-bottom: 11px;
  width: 124px;
  border: 1px solid #cdcdcd;
  text-align: center;
}

.example__position {
  margin: 0;
  position: absolute;
  bottom: -16px;
  left: 20px;
  width: 80px;
  background-color: @gray;

  .font(@main-font;
  12px;
  12px;
  400;
  @gray-text);

  text-transform: lowercase;
  text-align: center;
}

.example__price {
  display: block;
  margin: 0 auto;
  margin-top: 34px;
  margin-bottom: 40px;
  width: 200px;

  .font(@title;
  14px;
  20px;
  400;
  @black);
  text-transform: uppercase;

  text-align: center;
}

@media (min-width: @tablet-width) {
  .example {
    margin: 0;
    padding-top: 36px;
    background-color: @white;
  }

  .example__container {
    padding-top: 65px;
    width: 100%;
  }

  .example__title {
    display: block;
    width: 708px;
    margin: 0 auto;
    font-size: 60px;
    line-height: 60px;
  }

  .example__description-container {
    margin: 0 auto;
    width: 768px;
  }

  .example__info {
    margin: 0;
    margin-left: 30px;
    margin-bottom: 53px;
    font-size: 16px;
    line-height: 24px;
    width: 680px;
  }

  .example__description {
    justify-content: flex-start;
    margin: 0 30px;
    margin-bottom: 255px;
  }

  .example__result {
    margin-right: 42px;
    width: 162px;
    padding-top: 14px;
    padding-bottom: 19px;
    font-size: 30px;
    line-height: 37px;
  }

  .example__position {
    bottom: -7px;
    left: 12px;
    width: 135px;
    font-size: 14px;
    line-height: 14px;
    background-color: @white;
  }

  .example__price {
    margin: 0;
    margin-left: auto;
    width: 180px;
    font-size: 20px;
    line-height: 28px;
    text-align: right;
  }
}

@media (min-width: @desktop-width) {
  .example {
    margin: 0;
  }

  .example__container {
    width: 100%;
    padding-top: 0;
    background: linear-gradient(to right, #f2f2f2 50%, #e9e9e9 50%, #e9e9e9 100%);
    height: 462px;
  }

  .example__title {
    display: block;
    width: 1220px;
    margin: 0 auto;
    margin-bottom: 75px;
  }

  .example__wrapper {
    display: flex;
    width: 1220px;
    margin: 0 auto;
    padding-top: 71px;
    background: linear-gradient(to right, #f2f2f2 934px, #e9e9e9 934px, #e9e9e9 100%);
  }

  .example__description-container {
    flex-wrap: wrap;
  }

  .example__info {
    margin: 0;
    margin-bottom: 68px;
    width: 439px;
  }

  .example__description {
    margin: 0;
    width: 420px;
  }

  .example__result {
    margin-bottom: 56px;
  }

  .example__position {
    background-color: @gray;
  }

  .example__price {
    margin: 0;
    margin-right: 64px;
    width: 440px;
    text-align: left;
  }

  .example__price-description {
    margin-right: 60px;
  }
}
