.program {
  margin: 0 auto;
  width: 280px;
  margin-bottom: 30px;
}

.program__list {
  .list-reset;
}

.program__item {
  position: relative;
  padding: 5px 20px 22px;
  margin-bottom: 20px;
  width: 280px;
  height: 266px;

  background-color: @gray;
}

.program__item-title {
  position: absolute;
  top: 5px;
  left: 75px;
  padding-left: 10px;

  &::before {
    content: "";
    position: absolute;
    top: -6px;
    left: -53px;
    width: 50px;
    height: 51px;
    background-color: @green;
    border-radius: 50%;
  }
}

