.program__item-title--slim {
  &::after {
    content: "";
    position: absolute;
    bottom: -6px;
    right: 128px;
    width: 35px;
    height: 49px;
    background-image: url("../img/icon-cat-slim.svg");
    background-repeat: no-repeat;
  }
}

.program__item-description {
  margin-bottom: 16px;
  padding-top: 76px;

  &::after {
    content: "";
    display: inline-block;
    margin-top: 22px;
    width: 240px;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.1);
    background-repeat: no-repeat;
  }
}

.program__br {
  display: none;
}

.program__item-link {
  .font(@title;
  16px;
  16px;
  400;
  @black);

  text-transform: uppercase;

  &::after {
    content: "";
    display: inline-block;
    margin-left: 12px;
    width: 25px;
    height: 12px;
    background-image: url("../img/icon-right-arrow-long.svg");
    background-repeat: no-repeat;
  }

  &:hover,
  &:focus {
    &::after {
      content: "";
      display: inline-block;
      margin-left: 26px;
      width: 34px;
      height: 12px;
      background-image: url("../img/icon-right-arrow-verylong.svg");
      background-repeat: no-repeat;
    }
  }

  &:active {
    color: @black-opacity30;

    &::after {
      opacity: 0.3;
    }
  }
}

@media (min-width: @tablet-width) {
  .program {
    width: 708px;
    margin-bottom: 40px;
  }

  .program__item {
    padding: 42px 53px 50px;
    margin-bottom: 30px;
    height: 372px;
    width: 708px;
  }

  .program__item-title {
    top: 36px;
    left: 33px;
    font-size: 36px;
    line-height: 37px;
    padding-left: 21px;

    &::before {
      content: "";
      width: 200px;
      height: 200px;
      left: 413px;
      top: 14px;
    }
  }

  .program__item-title--pro {
    &::after {
      content: "";
      top: 58px;
      left: 379px;
      width: 268px;
      height: 113px;
    }
  }

  .program__item-title--slim {
    &::after {
      content: "";
      top: 2px;
      left: 444px;
      width: 141px;
      height: 194px;
    }
  }

  .program__item-description {
    font-size: 16px;
    line-height: 24px;
    width: 380px;
    padding-bottom: 39px;

    &::after {
      display: none;
    }
  }

  .program__br {
    display: initial;
  }

  .program__item-link {
    font-size: 20px;
    line-height: 20px;

    &::after {
      margin-left: 23px;
    }
  }
}

@media (min-width: @desktop-width) {
  .program {
    width: 1220px;
  }

  .program__list {
    display: flex;
    margin: 0 auto;
    width: 1220px;
    margin-bottom: 75px;
  }

  .program__item {
    width: 570px;
    padding: 95px 50px 44px;
    margin-right: 80px;
    margin-bottom: 0;

    &:last-child {
      margin-right: 0;
    }
  }

  .program__item-title {
    position: absolute;
    padding-left: 180px;
    top: 40px;

    &::before {
      content: "";
      width: 100px;
      height: 100px;
      left: 18px;
      top: -30px;
    }
  }

  .program__item-title--pro {
    &::after {
      content: "";
      top: -6px;
      left: 1px;
      width: 134px;
      height: 57px;
    }
  }

  .program__item-title--slim {
    &::after {
      content: "";
      top: -35px;
      left: 33px;
      width: 71px;
      height: 97px;
    }
  }

  .program__item-link {
    &::after {
      margin-left: 26px;
    }
  }

  .program__item-description {
    padding-top: 93px;
    padding-bottom: 31px;
    margin: 0;
    width: 464px;
  }

  .program__br {
    display: none;
  }
}
